<template>
  <div>
    <v-toolbar color="teal darken-3" class="dark--text" dark>
      <v-toolbar-title>Home</v-toolbar-title>
    </v-toolbar>
    <v-content>
      <v-container>
        <v-list two-line subheader>
          <v-subheader inset>Customer list</v-subheader>
          <v-list-tile
            v-for="(item) in items"
            :key="item.id"
            avatar
            :to="/customer/+item.id"
            ripple
          >
            <v-list-tile-avatar>
              <v-icon class="grey lighten-1 white--text">person</v-icon>
            </v-list-tile-avatar>

            <v-list-tile-content>
              <v-list-tile-title>{{ item.name }}</v-list-tile-title>
              <v-list-tile-sub-title>{{ item.subtitle }}</v-list-tile-sub-title>
            </v-list-tile-content>

            <v-list-tile-action>
              <v-btn icon ripple @click.prevent="del(item.id)">
                <v-icon color="teal lighten-1">delete</v-icon>
              </v-btn>
            </v-list-tile-action>
          </v-list-tile>
        </v-list>

        <v-btn absolute dark fab top right color="teal darken-1" @click="dialog = !dialog">
          <v-icon>add</v-icon>
        </v-btn>

        <v-dialog v-model="dialog" max-width="500px">
          <v-card>
            <v-card-text>
              <v-text-field label="Customer name" v-model="name"></v-text-field>
              <small class="grey--text">* This doesn't actually save.</small>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn flat color="teal darken-3" @click="submit(false)">Submit</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>

        <v-dialog v-model="loading" max-width="220px">
          <v-card>
            <v-card-title>
              <v-flex class="ma-3">
                <div class="title text-xs-center">
                  <span class="my-2">Loading...</span>
                </div>
                <div class="text-xs-center">
                  <v-progress-circular indeterminate color="teal darken-3" class="mt-3"></v-progress-circular>
                </div>
              </v-flex>
            </v-card-title>
          </v-card>
        </v-dialog>
      </v-container>
    </v-content>
  </div>
</template>

<script>
import Toolbar from "../components/Toolbar";
import firebase from "firebase";

export default {
  components: {
    Toolbar
  },
  data() {
    return {
      dialog: false,
      name: "",
      items: [],
      loading: false
    };
  },
  methods: {
    submit(con) {
      var db = firebase.firestore();
      //Add data database
      db.collection("Labours")
        .add({
          name: this.name
        })
        .then(docRef => {
          console.log("Document written with ID1: ", docRef.id);
          db.collection("LaboursData")
            .add({
              data: [],
              labourId: docRef.id
            })
            .then(function(docRef2) {
              console.log("Document written with ID2: ", docRef2.id);
            })
            .catch(function(error) {
              console.error("Error adding document: ", error);
            });
        })
        .catch(function(error) {
          console.error("Error adding document: ", error);
        });

      this.name = "";
      this.dialog = con;
    },
    del(userid) {
      console.log(userid);
      //this.items.splice(ind, 1);
      var db = firebase.firestore();
      var userRef = db.collection("Labours").doc(userid);
      userRef
        .delete()
        .then(function() {
          console.log("Document successfully deleted!");
          db.collection("LaboursData")
            .where("labourId", "==", userid)
            .get()
            .then(snapshot => {
              snapshot.forEach(doc => {
                db.collection("LaboursData")
                  .doc(doc.id)
                  .delete();
              });
            });
        })
        .catch(function(error) {
          console.error("Error removing document: ", error);
        });
    }
  },
  mounted() {
    this.loading = true;
    // data base se data le ke itemm name ki aarry me nmae ko store kya gya
    var db = firebase.firestore();
    db.collection("Labours").onSnapshot(snapshot => {
      snapshot.docChanges().forEach(change => {
        if (change.type === "added") {
          this.items.push({ id: change.doc.id, name: change.doc.data().name });
        }
        if (change.type === "removed") {
          const index = this.items.indexOf({
            id: change.doc.id,
            name: change.doc.data().name
          });
          this.items.splice(index, 1);
        }
      });
    });
    this.loading = false;
  }
};
</script>

